@use "../utils/mixins.scss";
@use "../utils/variables.scss";

.additions-container {
  padding: 25px 0 0;

  @media (min-width: variables.$tablet-width) {
    padding: 70px 0 75px;
  }

  @media (min-width: variables.$desktop-width) {
    padding: 76px 0 87px;
  }
}

.additions-container__wrapper {
  @media (min-width: variables.$desktop-width) {
    display: grid;
    grid-template-columns: 1fr 245px;
    gap: 80px;
  }
}

.additions-container__title {
  margin: 0 0 36px 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;

  @media (min-width: variables.$tablet-width) {
    margin: 0 0 80px 40px;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: variables.$desktop-width) {
    margin: 0 0 80px 0;
  }
}

.additions-container__list {
  @include mixins.list-reset;

  @media (min-width: variables.$tablet-width) {
    border: 1px solid #cdcdcd;
  }

  @media (min-width: variables.$desktop-width) {
    border-left: none;
    border-right: none;
  }
}
