@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
body {
  min-width: 320px;
  margin: 0;
  background-color: #ffffff;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.additional {
  margin: 0;
  padding: 0 0 34px;
}

.additional__title {
  margin: 0 0 34px;
  font-size: 24px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .additional__title {
    margin: 0 0 57px;
    font-size: 36px;
    line-height: 36px;
  }
}

.additional__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  gap: 13px;
}
@media (min-width: 768px) {
  .additional__list {
    grid-template-columns: 1fr 1fr;
    gap: 60px;
  }
}
@media (min-width: 1440px) {
  .additional__list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 80px;
  }
}

.additions-card {
  padding: 13px 0 19px;
  border-bottom: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .additions-card:last-child {
    border-bottom: none;
  }
}
.additions-card:first-child {
  border-top: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .additions-card:first-child {
    border-top: none;
  }
}
@media (min-width: 768px) {
  .additions-card {
    padding: 26px 38px 28px;
  }
}
@media (min-width: 1440px) {
  .additions-card {
    padding: 15px 0 15px;
    display: grid;
    grid-template-columns: 1fr 244px;
    gap: 40px;
  }
}

@media (min-width: 768px) {
  .additions-card__info {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .additions-card__info {
    display: grid;
    grid-template-columns: 325px 1fr;
    align-items: center;
  }
}

.additions-card__title {
  margin: 0 0 13px;
  font-size: 16px;
  line-height: 16px;
  color: #222222;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .additions-card__title {
    margin: 0 0 25px;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 1440px) {
  .additions-card__title {
    margin: 0;
  }
}

.additions-card__details-wrapper {
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .additions-card__details-wrapper {
    gap: 55px;
  }
}
@media (min-width: 1440px) {
  .additions-card__details-wrapper {
    margin: 0;
    gap: 0;
    justify-content: start;
  }
}

.additions-card__details-item {
  font-family: "Lato";
  text-align: center;
}
@media (min-width: 1440px) {
  .additions-card__details-item:first-child {
    width: 235px;
    text-align: start;
  }
}
@media (min-width: 768px) {
  .additions-card__details-item {
    font-size: 16px;
    line-height: 20px;
  }
}

.additions-card__button {
  width: 100%;
}
@media (min-width: 1440px) {
  .additions-card__button {
    width: 245px;
  }
}

.additions-container {
  padding: 25px 0 0;
}
@media (min-width: 768px) {
  .additions-container {
    padding: 70px 0 75px;
  }
}
@media (min-width: 1440px) {
  .additions-container {
    padding: 76px 0 87px;
  }
}

@media (min-width: 1440px) {
  .additions-container__wrapper {
    display: grid;
    grid-template-columns: 1fr 245px;
    gap: 80px;
  }
}

.additions-container__title {
  margin: 0 0 36px 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .additions-container__title {
    margin: 0 0 80px 40px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .additions-container__title {
    margin: 0 0 80px 0;
  }
}

.additions-container__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .additions-container__list {
    border: 1px solid #cdcdcd;
  }
}
@media (min-width: 1440px) {
  .additions-container__list {
    border-left: none;
    border-right: none;
  }
}

.advantages {
  padding-bottom: 50px;
}
@media (min-width: 768px) {
  .advantages {
    padding-bottom: 47px;
  }
}
@media (min-width: 1440px) {
  .advantages {
    padding-bottom: 70px;
  }
}

.advantages__title {
  margin: 0 0 40px 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .advantages__title {
    font-size: 60px;
    line-height: 60px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  gap: 20px;
}
@media (min-width: 768px) {
  .advantages__list {
    grid-template-columns: 325px 1fr;
    gap: 40px;
    counter-reset: li;
  }
}
@media (min-width: 1440px) {
  .advantages__list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.advantages__item {
  min-height: 60px;
  background-repeat: no-repeat;
  position: relative;
}
@media (min-width: 768px) {
  .advantages__item {
    min-height: 280px;
    background-position-y: 40px;
    counter-increment: li;
  }
  .advantages__item::after {
    content: counter(li);
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
    position: absolute;
    top: 0;
    left: 100px;
    z-index: -1;
  }
}
.advantages__item--material {
  background-image: url("../img/index-advantages/advantages-material.svg");
}
@media (min-width: 768px) {
  .advantages__item--material {
    background-image: url("../img/index-advantages/advantages-material-tablet.svg");
  }
}
.advantages__item--form {
  background-image: url("../img/index-advantages/advantages-form.svg");
}
@media (min-width: 768px) {
  .advantages__item--form {
    background-image: url("../img/index-advantages/advantages-form-tablet.svg");
  }
}
.advantages__item--order {
  background-image: url("../img/index-advantages/advantages-order.svg");
}
@media (min-width: 768px) {
  .advantages__item--order {
    background-image: url("../img/index-advantages/advantages-order-tablet.svg");
  }
}
.advantages__item--results {
  background-image: url("../img/index-advantages/advantages-results.svg");
}
@media (min-width: 768px) {
  .advantages__item--results {
    background-image: url("../img/index-advantages/advantages-results-tablet.svg");
  }
}

.advantages__item-description {
  font-family: "Lato", sans-serif;
  margin: 0 0 0 80px;
  line-height: 18px;
  color: #444444;
}
@media (min-width: 768px) {
  .advantages__item-description {
    margin: 151px 80px 0 0;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .advantages__item-description {
    margin: 151px 7px 0 0;
  }
}

.button-common {
  font-family: "Oswald";
