  }
}

.catalog-wrapper__title {
  margin: 0 0 41px 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .catalog-wrapper__title {
    margin: 0 0 84px 0;
