  }
}

.checkbox {
  padding-left: 45px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  cursor: pointer;
}
@media (min-width: 768px) {
  .checkbox {
    padding-left: 60px;
  }
}

.checkbox__input[type=checkbox]:checked + .checkbox__mark::after {
  position: absolute;
  top: 6px;
  left: 4px;
  width: 18px;
  height: 14px;
  content: url("../img/checkbox-mobile.svg");
}
@media (min-width: 768px) {
  .checkbox__input[type=checkbox]:checked + .checkbox__mark::after {
    top: 10px;
    left: 8px;
    width: 24px;
    height: 18px;
    content: url("../img/checkbox-tablet.svg");
  }
}

.checkbox__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .checkbox__mark {
    width: 40px;
    height: 40px;
  }
}

.checkbox__text {
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
  padding: 6px 0;
}
@media (min-width: 768px) {
  .checkbox__text {
    font-size: 20px;
    line-height: 30px;
  }
}

.comments {
