    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/catalog-promo/gift-promo-tablet.png");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .gift-promo__wrapper {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/catalog-promo/gift-promo-tablet@2x.png");
