.input {
  display: flex;
  gap: 13px;
  align-items: center;
  margin: 0;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .input {
    font-size: 20px;
    line-height: 30px;
  }
}

.input__control[type=number] {
  appearance: textfield;
}

.input__control {
  flex-grow: 1;
  padding: 14px 15px 13px;
  font-family: inherit;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
  border: 1px solid #e7e7e7;
  resize: vertical;
}
